
.avatar {
  display: inline-block;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-image: url('../assets/avatar.jpg');
  border: 1px solid #e5e5e5;
  margin-bottom: 2em;
}





